import React from 'react'
import './PoliticaDevolucion.scss'
import Footer from '../../components/Footer/Footer'
import CopyRight from '../../components/CopyRight/CopyRight'

export default function PoliticaDevolucion() {
  return (
    <>
        <div className='container politicas'>
            <p>
                <h4>Polticas de Devolucin</h4>
                <h5>Politicas de cambios/devoluciones de ETANA Buenos Aires:</h5>
                <span>
                    1- Se realizarn cambios nicamente dentro de los 30 (treinta) das de efectuada la compra.
                </span>
                <span>
                    2- ETANA Buenos Aires realiza un estricto control de calidad al momento de recibir mercadera y enviar/entregar sus productos, por ende sabe reconocer con facilidad posibles fallas de origen.
                </span>
                <span>
                    3- Los gastos implicados en la logstica por cambio de productos o talles corrern por cuenta de quien compra.  
                </span>
                <span>
                    4- No se realizarn devoluciones de dinero en ninguno de los casos. 
                </span>
                <span>
                    El equipo de ETANA Buenos Aires se encuentra a disposicin por cualquier inquietud.
                </span>
            </p>
        </div>
        <Footer />
        <CopyRight />
    </>
  )
}
